* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

$main-color: #69510F;

.fourthPage {
    height: 100vh;
    width: 100%;
    background-color: black;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr;

    .containerOne {
        position: relative;

        img {
            position: absolute;
            height: 64px;
            left: 50%;
            top: 20%;
            transform: translate(-50%, -50%);
        }

        h1 {
            position: absolute;
            left: 50%;
            top: 35%;
            transform: translate(-50%, -50%);
            color: $main-color;
            font-size: 1rem;
        }

        p {
            position: absolute;
            left: 50%;
            top: 57.5%;
            transform: translate(-50%, -50%);
            color: whitesmoke;
            text-align: center;
            font-size: 0.75rem;
        }
    }

    .containerTwo {
        position: relative;

        .fastService {
            position: absolute;
            height: 64px;
            left: 50%;
            top: 20%;
            transform: translate(-50%, -50%);
        }

        h1 {
            position: absolute;
            left: 50%;
            top: 35%;
            transform: translate(-50%, -50%);
            color: $main-color;
            font-size: 1rem;
        }

        p {
            position: absolute;
            left: 50%;
            top: 57.5%;
            transform: translate(-50%, -50%);
            color: whitesmoke;
            text-align: center;
            font-size: 0.75rem;
        }

        .arrowDownThree {
            height: 26px;
            position: absolute;
            left: 50%;
            bottom: 0 !important;
            transform: translate(-50%, -50%);
        }
    }
}